import React from "react";
import { Card } from "react-bootstrap";
import "./datacard.css";

const Datacard = (props) => {
  return (
    <Card className="datacard" id={props.name}>
      <Card.Body>
        <b>{props.header}</b>
        <br />
        {props.body}
      </Card.Body>
    </Card>
  );
};

export default Datacard;
