import Cards from "./Cards";
import React from "react";
import Visuals from "./Visuals";

const General = (props) => {
  if (props.data2.length > 0)
    return (
      <div
        className="d-flex flex-column align-items-center"
        style={{ width: "100%" }}
      >
        <Cards
          confirmed={props.data2[0].confirmed}
          recovered={props.data2[0].recovered}
          deceased={props.data2[0].deaths}
        />
        <Visuals data={props.data} />
      </div>
    );
  else return <div></div>;
};

export default General;
