import React from "react";
import { ResponsiveLine } from "@nivo/line";
import "./visuals.css";

const Visuals = (props) => {
  let data = [
    {
      id: "confirmed",
      color: "red",
    },
  ];

  data[0].data = props.data.map((item) => {
    return { y: parseInt(item.dailyconfirmed), x: item.date };
  });

  return (
    <div className="charts">
      <ResponsiveLine data={data} />
    </div>
  );
};

export default Visuals;
