import React from "react";
import Datacard from "./Datacard";
import { Container } from "react-bootstrap";
import "./Cards.css";

const Cards = (props) => {
  return (
    <Container className="d-flex justify-content-center">
      <Datacard header="Confimed" body={props.confirmed} name="confirmed" />
      <Datacard header="Recovered" body={props.recovered} name="recovered" />
      <Datacard header="Deceased" body={props.deceased} name="deceased" />
    </Container>
  );
};

export default Cards;
