import React from "react";
import General from "./Components/General";
import Statewise from "./Components/Statewise";
import axios from "axios";
import "./App.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      statewise: [],
      casesTimeSeries: [],
    };
  }
  componentDidMount() {
    setInterval(() => {
      axios.get("https://api.covid19india.org/data.json").then((response) => {
        this.setState({
          statewise: response.data.statewise,
          casesTimeSeries: response.data.cases_time_series,
        });
      });
    }, 2000);
  }
  render() {
    return (
      <div className="App d-flex flex-column align-items-center">
        <p id="title">
          <b>CovidTrack19</b>
        </p>
        <General
          data={this.state.casesTimeSeries}
          data2={this.state.statewise}
        />

        <Statewise data={this.state.statewise} />
      </div>
    );
  }
}

export default App;
